.app-root {
  min-height: 100vh;
  color: #fff;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
}

.search-wrapper {
  margin: 3rem auto 0;
  width: 90%;
  max-width: 600px;
}

.search-input {
  width: 100%;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 9999px;
  outline: none;
}

.widgets {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 600px));
  justify-content: center;
  gap: 1rem;
  width: 100%;
  padding: 2rem;
  box-sizing: border-box;
  align-content: center;
}

.widget {
  width: 100%;
}

.now-playing-container,
.calendar-container {
  width: 100%;
}

.nothing-playing {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  font-weight: 600;
  color: #9a9a9a;
  text-align: center;
  padding: 2rem;
  border-radius: 1rem;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  border: 0px solid rgba(255, 255, 255, 0);
  max-width: 30%;
  max-height: 50px;
  margin: 15rem auto 0;
}